.card{
  height: 140px;
  width: 180px;
  margin-right: 15px;
}

.selected-card{
  height: 140px;
  width: 180px;
  margin-right: 50px;
  margin-left: 50px;

}


.title-game {
  margin-bottom: 10px;
  margin-top: 2px;
}

.game-modal{
  background-color: #110F1E !important;
  border: transparent !important;
}

.tittle-main {
  margin-bottom:  70px;
  font-weight: 900;
  border-bottom: solid #110f1e;
}

.round-container {
  margin-bottom: 20px;
}

.label-round {
  display: inline-block;
  margin-right:15px;
  background-color: black;
  border-radius: 4px;
  padding: 4px;
  color: #ffa86a;
}

.value-rounds{
  background-color: transparent;
  border: 1px solid grey;
  border-radius: 4px;
  padding: 5px;
  color: #ffa86a;

}

.button-container {
  width: 300px;
}

.main-page-container {
  margin-bottom: 1050px !important;
  margin-top: 100px !important;
  border: solid #110f1e;
  border-radius: 5px;
  max-width: 45% !important;
  background-color: #222129 !important;

}

.rules-item{
  color: #ffa86a !important;
  background-color: #3b3b42!important;
  border-color: #222129!important;
}

.rules-item-wins-text {
  color: green;
}
.rules-item-losses-text{
  color: red;
}


.container-header{
  background-color: #110F1E;
  height: 80px;
}

.logo-header{
  color: var(  --main-color-white);
  cursor: pointer;
  letter-spacing: 2px;

}



body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #222129;
  color: #ffa86a;
  font-family: Fira Code,Monaco,Consolas,Ubuntu Mono,monospace;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

